<template>
    <div class="arena">
        <div class="health">
            <div class="you" v-bind:style="{ width: yourHelth + '%' }"></div>
            <div class="enemy" v-bind:style="{ width: enemyHelth + '%' }"></div>
        </div>
        <div class="attack">
            <div class="you"></div>
            <div class="enemy"></div>
        </div>
        <div class="warrior you" v-bind:class="dieY"></div>
        <div class="warrior enemy" v-bind:class="dieE"></div>
        <div class="wrap">
            <div class="post" v-for="log in logs">
                <div>{{ log.text }}</div>
            </div>
        </div>
        <button @:click="fight">fight</button>
    </div>
</template>

<script>

export default {

    data() {
        return {
            text: 'fight',
            fightInterval: "",
            round_length: 1000,
            warriors: {
                1: {
                    name: "you",
                    health: 0
                },
                2: {
                    name: "enemy",
                    health: 0
                }
            },
            logs: [],
            yourHelth: 0,
            enemyHelth: 0,
            result: '',
            dieY: '',
            dieE: '',
            enemyTime: "",
            winner: ""
        }
    },
    emits: ['remove'],
    methods: {
        destr() {
            this.$emit('destr', {
                winner: this.winner
            })
        },

        youAttack() {
            let yourWar = document.querySelector(".warrior.you");
            yourWar.animate({
                left: "40%"
            }, 300, () => {
                yourWar.animate({
                    left: "33%"
                });
            });
            let youAttack = Math.floor(Math.random() * 6);
            this.warriors[2].health -= youAttack;
            this.result = "you dealt " + youAttack + " damage to an enemy, left " + this.warriors[2].health
            this.logs.push({ text: this.result })


        },
        enemyAttack() {
            let enemyWar = document.querySelector(".warrior.enemy");
            enemyWar.animate({
                right: "40%"
            }, 300, () => {
                enemyWar.animate({
                    right: "33%"
                });
            });
            let enemyAttack = Math.floor(Math.random() * 6);
            this.warriors[1].health -= enemyAttack;

            this.result = "enemy dealt " + enemyAttack + " damage to an you, left " + this.warriors[1].health
            this.logs.push({ text: this.result })


        },


        initialize() {
            this.logs = []
            this.dieY = ''
            this.dieE = ''
            this.warriors[1].health = Math.floor(Math.random() * 11) + 10
            this.warriors[2].health = Math.floor(Math.random() * 11) + 10
            this.yourHelth = this.warriors[1].health * 2.5;
            this.enemyHelth = this.warriors[2].health * 2.5;


        },

        endGame() {
            if (this.warriors[1].health <= 0) {
                this.dieY = 'dieY'

            } else {
                this.dieE = 'dieE'

            }
        },

        isClass() {
            if (this.dieY == 'dieY' || this.dieE == 'dieE') this.$.appContext.app.unmount()
        },

        fight() {

            this.initialize()

            this.fightInterval = setInterval(() => {
                this.youAttack()
                if (this.warriors[2].health <= 0) {
                    this.result = "Enemy die..."
                    this.winner = "You win!"
                    this.destr()
                    this.logs.push({ text: this.result })
                    this.endGame()
                    // console.log(this.winner)
  
                    clearInterval(this.fightInterval);
                    clearTimeout(this.enemyTime)



                } else {
                    this.enemyTime = setTimeout(() => {
                        this.enemyAttack()
                        if (this.warriors[1].health <= 0) {
                            this.result = "You die..."
                            this.winner = "Enemy win!"
                            this.destr()
                            this.logs.push({ text: this.result })
                            this.endGame()
                            clearInterval(this.fightInterval);
                            clearTimeout(this.enemyTime)

                        }
                    }, 500);
                }



            }, 1000)




        }
    },
    updated() {
        this.enemyHelth = this.warriors[2].health * 2.5;
        this.yourHelth = this.warriors[1].health * 2.5;
        

    }
    

}

</script>

<style lang="scss">
* {
    margin: 0;
    padding: 0;
    border: 0;
}

html,
body {
    height: 100%;
}

.arena {
    height: 350px;
    width: 700px;
    background: #ddd;
    position: relative;
    margin: auto;


    .health {
        position: absolute;
        top: 10px;
        left: 0;
        height: 10px;
        width: 100%;

        .you {
            left: 0;
            background: red;
            width: 50%;
            height: 100%;
            position: absolute;
            transition: all .5s ease;
            -webkit-transition: all .5s ease;
        }

        .enemy {
            right: 0;
            background: red;
            width: 50%;
            height: 100%;
            position: absolute;
            transition: all .5s ease;
            -webkit-transition: all .5s ease;
        }
    }

    .attack {
        position: absolute;
        top: 50px;
        left: 0;
        height: auto;
        width: 100%;
        text-align: center;

        .you {
            left: 0;
            width: 33.3%;
            position: absolute;
        }

        .enemy {
            left: 66.6%;
            width: 33.3%;
            position: absolute;
        }
    }

    .warrior {
        height: 100px;
        width: 50px;

        position: absolute;
        top: 65%;

        &.you {
            background-image: url("@/img/you.png");
            left: 33%;

            &.dieY {
                height: 100px;
                width: 100px;
                background-image: url("@/img/youDie.png");
            }
        }

        &.enemy {
            background-image: url("@/img/enemy.png");
            right: 33%;

            &.dieE {
                height: 100px;
                width: 100px;
                background-image: url("@/img/enemyDie.png");
            }
        }
    }

    button {
        position: absolute;
        top: 110%;
        right: 40%;
        display: inline-block;
        outline: none;
        cursor: pointer;
        font-size: 16px;
        line-height: 20px;
        font-weight: 600;
        border-radius: 8px;
        padding: 13px 23px;
        border: 1px solid #222222;
        transition: box-shadow 0.2s ease 0s, -ms-transform 0.1s ease 0s, -webkit-transform 0.1s ease 0s, transform 0.1s ease 0s;
        background: #fff;
        color: #222222;

        &:hover {
            border-color: #000000;
            background: #f7f7f7;
        }

    }

    .wrap {
        position: absolute;
        top: 130%;
        right: 25%;
    }
}
</style>
