<template>
    <div class="app">
        <button class="btn" @:click="listener">Begin</button>
        <component :is="component" @destr="isDestr"></component>
    </div>
</template>

<script>
import Arena from "@/components/Arena";

export default {
    props: ['isDestr'],
    components: {
        Arena
    },
    data() {
        return {
            component: "",
            listener: null,
            winner: ""
        }
    },
    methods: {
        initAren() {
            this.component = "Arena"
            const element = document.querySelector(".btn");
            element.remove();
        },
        isDestr(data) {
            
            // alert(this.winner)
            if (data) setTimeout(this.$.appContext.app.unmount, 500)
            this.winner = data.winner
        }

    },
    mounted() {
        this.listener = this.initAren
    },
    beforeUnmount() {
        alert(this.winner)
    }
}
</script>

